import React from "react";
import { connect } from "react-redux";
import { logout } from "../redux/asyncActions";
import { closePopup } from "../redux/actions";
import { css } from "glamor";
import ClickOutside from "react-click-outside";

const classes = {
  box: css({
    width: "100px",
    height: "auto",
    paddingTop: "4px",
    paddingBottom: "4px",
    background: "white",
    borderRadius: "4px",
    border: "1px solid #e6ecf0",
    boxShadow: "0px 0px 4px 0px rgba(0,0,0,0.1)",
    zIndex: "100000"
  }),
  ul: css({
    padding: "0px",
    marginTop: "4px",
    marginBottom: "4px"
  }),
  li: css({
    listStyle: "none",
    padding: "4px 8px",
    cursor: "pointer",
    fontSize: "12px",
    fontWeight: "bold",
    ":hover": {
      background: "rgba(240, 240, 240, 1.0)"
    }
  })
};

const Dropdown = props => {
  return (
    <ClickOutside onClickOutside={() => props.closePopup()}>
      <div className={classes.box}>
        <ul className={classes.ul}>
          <li className={classes.li} onClick={() => props.logout()}>
            Logout
          </li>
        </ul>
      </div>
    </ClickOutside>
  );
};

const mapStateToProps = state => {
  return {
    popupComponent: state.app.popupComponent,
    isPopupShown: state.app.isPopupShown
  };
};

const mapDispatchToProps = dispatch => {
  return {
    closePopup: () => {
      dispatch(closePopup());
    },
    logout: () => {
      dispatch(logout());
    }
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Dropdown);
